import { writable, derived } from "svelte/store";
/** reactive version of {@link window.location.hash} */
export const urlHash = writable(window.location.hash);
window.addEventListener("popstate", () => urlHash.set(window.location.hash), false);
export const shortCodeFromUrl = derived(urlHash, ($urlHash) => {
    return $urlHash.startsWith("#/") ? $urlHash : undefined;
});
